import "./About.css";

const AboutMe = () => {
  // for cp add the below in description
  // <span  id="user-detail-name">Hello, I'm Salil Upadhyay</span>. <span  id="user-detail-intro"> HTML, CSS, JavaScript </span>
  
  const description = `<p>
  Hello, I'm Salil Upadhyay. 
  I'm a Web Developer based in Sagar,India. I'm passionate about building web applications and experimenting with new technologies.
    In my spare time I like to play online games, capturing photographs, reading and travelling. I’m always down for hearing about new projects, so feel free to drop me a line, 
    I hope you enjoy browsing my portfolio, and I look forward to the opportunity to bring your next web development project to life.
    </br>
    Let's build cool things!</p>`;
  return (
    <div>
      <main id="about" className="about section">
        <div className="aboutMe-container">
          <div className="abouMe-row">
            <div
              className=" col-lg-6 col-md-5 col-sm-12 about-img "
              data-aos="fade-up-right"
            >
              <img
                src="profile-pic.jpeg"
                style={{ borderRadius: "20px" }}
                alt=""
                className="home-img"
              />
            </div>
            <div
              className=" col-lg-6 col-md-7  col-sm-12 about_myinfo"
              data-aos="fade-up-left"
            >
              <div className="title">
                <h2>About Me</h2>
              </div>
              <div className="about-description">
                <div id="foo">
                  <div
                    dangerouslySetInnerHTML={{
                      __html: `${description}`,
                    }}
                  />
                </div>
              </div>

              <div className="itscv">
                <div>
                  <button
                    className="my-cv nav-link resume download-cv"
                    id="resume-button-2"
                    onClick={() => {
                      window.open(
                        "https://drive.google.com/file/d/10iArQ7pjCqLluP_LSxb7Qm66Cb44p5OA/view?usp=share_link",
                        "_blank"
                      );
                    }}
                  >
                    <a
                      id="resume-link-2"
                      href={"Salil_Upadhyay_Resume.pdf"}
                      download="Salil_Upadhyay_Resume.pdf"
                      target="_blank"
                      rel="noreferrer"
                    >
                      <span style={{ marginRight: "5px" }}>RESUME</span>
                      <i className="bx bx-download"></i>
                    </a>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
};

export default AboutMe;
