import "./Navbar.css";
import { Link } from "react-scroll";
import { useState } from "react";

const Navbar = () => {
  //handlin hamburger icon for smaller devices
  const [show, setShow] = useState(false);
  const [check, setCheck] = useState(false);
  const handleHamburger = () => {
    setShow((prev) => !prev);
  };

  // hadling theme button
  const handleTheme = () => {
    if (document.body.classList.contains("light")) {
      document.body.classList.remove("light");
      setCheck((prev) => !prev);
    } else {
      document.body.classList.add("light");
      setCheck((prev) => !prev);
    }
  };
  return (
    <div>
      <button type="button" className="mobile-nav-toggle d-lg-none">
        <i className="icofont-navigation-menu"></i>
      </button>
      <header id="nav-menu" className="fixed-top">
        <div className="container-fluid  navbur">
          <div className="navi">
            <div className="col-xl-12 d-flex align-items-center lefty">
              <h5 className="ld-flex mr-auto devman">
                <img
                  src="https://code.google.com/images/developers.png"
                  alt=""
                />
                salil <span className="blink"> _</span>
              </h5>
              <nav
                className={show ? "nav-menu mainMenu act" : "nav-menu mainMenu"}
              >
                <ul>
                  <li className="">
                    <Link
                      to="home"
                      spy={true}
                      smooth={true}
                      duration={500}
                      isDynamic={true}
                      offset={-40}
                      onClick={handleHamburger}
                      className="nav-link home"
                    >
                      Home
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="about"
                      spy={true}
                      smooth={true}
                      offset={-30}
                      duration={600}
                      onClick={handleHamburger}
                      className="nav-link about"
                    >
                      About
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="skills"
                      spy={true}
                      smooth={true}
                      offset={-70}
                      duration={700}
                      onClick={handleHamburger}
                      className="nav-link skills"
                    >
                      Skills
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="projects"
                      spy={true}
                      smooth={true}
                      offset={-100}
                      duration={800}
                      onClick={handleHamburger}
                      className="nav-link projects"
                    >
                      Projects
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="contact"
                      spy={true}
                      smooth={true}
                      duration={900}
                      onClick={handleHamburger}
                      className="nav-link contact"
                    >
                      Contact
                    </Link>
                  </li>
                </ul>
              </nav>
              <div className="nav-social">
                <a
                  id="contact-github"
                  href="https://github.com/salil-01"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fa fa-github"></i>
                </a>
              </div>
              <div className="left-btns">
                <div className="" id="theme-button2" >
                  <input
                    id="toggle"
                    className="toggle"
                    type="checkbox"
                    defaultChecked={check}
                    onChange={handleTheme}
                  ></input>
                </div>
                <div>
                  <div className="CvMe">
                    <button
                      className="my-cv nav-link resume"
                      id="resume-button-1"
                      onClick={() => {
                        window.open(
                          "https://drive.google.com/file/d/10iArQ7pjCqLluP_LSxb7Qm66Cb44p5OA/view?usp=share_link",
                          "_blank"
                        );
                      }}
                    >
                      <a
                        id="resume-link-1"
                        href={"Salil_Upadhyay_Resume.pdf"}
                        download="Salil_Upadhyay_Resume.pdf"
                        target="_blank"
                        rel="noreferrer"
                      >
                        <span style={{ marginRight: "5px" }}>Resume</span>
                        <i className="bx bx-download"></i>
                      </a>
                    </button>
                  </div>
                </div>
                <div id="theme-button">
                  <button
                    className={show ? "menuBtn act" : "menuBtn"}
                    onClick={handleHamburger}
                  >
                    <span className="lines"></span>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </header>
    </div>
  );
};

export default Navbar;
