// import { useGetProjectsQuery } from "../../Api/api";
import { useEffect, useState } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import "./Projects.css";
import React from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import { projectData, projectData2 } from "./projectData";
import { Link } from "react-router-dom";
export const Projects = () => {
  //setting up project details
  const projects = projectData;
  const projects2 = projectData2;
  const [projectsDetails, setProjectsDetails] = useState(projects);
  const [projectsDetails2, setProjectsDetails2] = useState(projects2);

  //options for owl carousel
  const options = {
    margin: 30,
    responsiveClass: true,
    nav: true,
    dots: true,
    autoplay: true,
    loop: true,
    autoplayTimeout: 3000,
    autoplayHoverPause: true,
    navText: [
      `<div class='nav-btn prev-slide'><</div>`,
      `<div class='nav-btn next-slide'>></div>`,
    ],
    smartSpeed: 1000,
    responsive: {
      0: {
        items: 1,
      },
      310: {
        items: 1,
      },
      500: {
        items: 1,
      },
      600: {
        items: 1,
      },
      740: {
        items: 2,
      },
      1000: {
        items: 2.7,
      },
      1300: {
        items: 3,
      },
      1440: {
        items: 3,
      },
    },
  };

  // useeffect to initialize aos and projects data on component mount
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  }, []);
  useEffect(() => {
    setProjectsDetails(projects);
    setProjectsDetails2(projects2);
  }, [projects, projectsDetails, projects2, setProjectsDetails2]);

  return (
    <div className="mywork " id="projects">
      <div className="mywork-title">
        <h2>Check Out My Recent Projects</h2>
        <h3>My Work</h3>
      </div>
      <div className="project-row">
        {projectsDetails?.length && (
          <OwlCarousel className="owl-carousel owl-theme" {...options}>
            {projectsDetails?.map((details) => (
              <div
                className="project project-card"
                key={details.id}
                data-aos="fade-up"
              >
                <div className="project-img">
                  <img src={details.about_avatar} alt="" className="work-img" />
                </div>

                <div className="date-posted">
                  <div className="who-post">
                    <p className="admin project-tech-stack">
                      {details.language_used}
                    </p>
                  </div>
                </div>
                <div className="work-details">
                  <h2 className="project-title">{details.Project_title}</h2>
                  <p className="work-info project-description">
                    {details.Project_info}
                  </p>
                  <div className="project-links">
                    <Link
                      to={details.demo_link}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="project-deployed-link"
                    >
                      <h6 className="learnmore">
                        <i className="fa fa-laptop" aria-hidden="true"></i>
                        &nbsp; Live Demo
                      </h6>
                    </Link>
                    <Link
                      to={details.project_link}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="project-github-link"
                    >
                      <h6 className="learnmore">
                        <i className="fa fa-github" aria-hidden="true"></i>{" "}
                        &nbsp; Source Code
                      </h6>
                    </Link>
                  </div>
                </div>
              </div>
            ))}
          </OwlCarousel>
        )}
      </div>
      <div className="project-row">
        {projectsDetails2?.length && (
          <OwlCarousel className="owl-carousel owl-theme" {...options}>
            {projectsDetails2?.map((details) => (
              <div
                className="project project-card"
                key={details.id}
                data-aos="fade-up"
              >
                <div className="project-img">
                  <img src={details.about_avatar} alt="" className="work-img" />
                </div>

                <div className="date-posted">
                  <div className="who-post">
                    <p className="admin project-tech-stack">
                      {details.language_used}
                    </p>
                  </div>
                </div>
                <div className="work-details">
                  <h2 className="project-title">{details.Project_title}</h2>
                  <p className="work-info project-description">
                    {details.Project_info}
                  </p>
                  <div className="project-links">
                    <Link
                      to={details.demo_link}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="project-deployed-link"
                    >
                      <h6 className="learnmore">
                        <i className="fa fa-laptop" aria-hidden="true"></i>
                        &nbsp; Live Demo
                      </h6>
                    </Link>
                    <Link
                      to={details.project_link}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="project-github-link"
                    >
                      <h6 className="learnmore">
                        <i className="fa fa-github" aria-hidden="true"></i>{" "}
                        &nbsp; Source Code
                      </h6>
                    </Link>
                  </div>
                </div>
              </div>
            ))}
          </OwlCarousel>
        )}
      </div>
    </div>
  );
};

// export default Projects;
